import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { iif, Observable, of } from 'rxjs';
import {
  IRecordGridColumn,
  IRecordGridConfig,
  IRecordGridDataPageParams,
  RecordGridComponent
} from '@helix/platform/view/components';
import { RX_APPROVAL_CONSOLE } from '../approval-console.constant';
import { IDataPageResult } from '@helix/platform/shared/api';
import { includes, noop, omit } from 'lodash';
import {
  IRecordDefinition,
  RX_RECORD_DEFINITION,
  RxRecordInstanceService,
  RxRecordInstanceDataPageService
} from '@helix/platform/record/api';
import { TranslateService } from '@ngx-translate/core';
import {
  ApprovalGridType,
  ApprovalRequestStatus,
  IApprovalRequest,
  INeedAttentionRequest
} from '../approval-console.types';
import { ApprovalRequestQuestionComponent } from '../approval-request-question/approval-request-question.component';
import { IRowDataItem } from '@helix/platform/view/api';
import { switchMap } from 'rxjs/operators';
import { RxApprovalConsoleService } from '../approval-console.service';
import { ReadOnlyFieldsModalComponent, RxModalService } from '@helix/platform/ui-kit';
import { AdaptModalService } from '@bmc-ux/adapt-angular';

@Component({
  selector: 'rx-approval-request-questions',
  templateUrl: './approval-request-questions.component.html'
})
export class ApprovalRequestQuestionsComponent implements OnInit, OnChanges {
  @Input()
  approvalRequest: IApprovalRequest | INeedAttentionRequest;

  @Input()
  requestsTabStatus: ApprovalRequestStatus;

  @Input()
  gridType: ApprovalGridType;

  @Output()
  questionSubmitted: EventEmitter<void> = new EventEmitter();

  @ViewChild('requestQuestionsGrid')
  requestQuestionsGrid: RecordGridComponent;

  gridConfig$: Observable<IRecordGridConfig>;

  constructor(
    private translateService: TranslateService,
    private rxRecordInstanceService: RxRecordInstanceService,
    private rxModalService: RxModalService,
    private adaptModalService: AdaptModalService,
    private rxApprovalConsoleService: RxApprovalConsoleService,
    private rxRecordInstanceDataPageService: RxRecordInstanceDataPageService
  ) {}

  ngOnInit(): void {
    this.gridConfig$ = of({
      enableRowSelection: null,
      getData: (queryParams: IRecordGridDataPageParams) => this.getQuestionData(queryParams),
      columns: this.getQuestionColumns(),
      getRecordDefinition: () => of(this.getQuestionRecordDefinition()),
      recordDefinitionName: RX_APPROVAL_CONSOLE.requestDetailsGrid.definition,
      styles: 'flex-fill'
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.approvalRequest?.currentValue) {
      this.requestQuestionsGrid?.api.refresh().subscribe();
    }
  }

  isNewQuestionButtonDisabled(): boolean {
    return !includes([ApprovalRequestStatus.Pending, ApprovalRequestStatus.Hold], this.requestsTabStatus);
  }

  askNewQuestion(): void {
    this.rxModalService
      .openModal({
        content: ApprovalRequestQuestionComponent,
        size: 'sm',
        data: {
          selectedRequest: this.approvalRequest
        }
      })
      .then((result) => {
        if (result) {
          this.questionSubmitted.emit();
        }
      })
      .catch(noop);
  }

  private getQuestionData(queryParams: IRecordGridDataPageParams): Observable<IDataPageResult> {
    return iif(
      () => this.gridType === ApprovalGridType.NeedAttentionRequests,
      this.rxApprovalConsoleService.getCurrentRequestDetails(
        this.approvalRequest[RX_APPROVAL_CONSOLE.needAttentionRequestsGrid.fields.requestID],
        this.approvalRequest[RX_APPROVAL_CONSOLE.needAttentionRequestsGrid.fields.application]
      ),
      of(this.approvalRequest as IApprovalRequest)
    ).pipe(
      switchMap((currentRequest: IApprovalRequest) =>
        this.rxRecordInstanceDataPageService.post({
          params: omit(
            {
              ...queryParams,
              shouldIncludeTotalSize: true,
              [RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.application]: currentRequest.application,
              [RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.type]:
                RX_APPROVAL_CONSOLE.requestDetailsGrid.questionTypeValue,
              [RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.request]: currentRequest.request
            },
            ['searchText']
          )
        })
      )
    );
  }

  private getQuestionColumns(): IRecordGridColumn[] {
    return [
      {
        index: 0,
        fieldId: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.to.toString(),
        title: this.translateService.instant(
          'com.bmc.arsys.rx.client.approval.console.questions.grid.column.question-to.title'
        )
      },
      {
        index: 1,
        fieldId: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.from.toString(),
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.from.label')
      },
      {
        index: 2,
        fieldId: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.question.toString(),
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.question.label'),
        clickable: true,
        actions: [
          {
            name: (previousAction: any, selectedRow: IRowDataItem) => {
              this.showQuestionDetails(selectedRow);
            }
          }
        ],
        filterable: false,
        sortable: false
      },
      {
        index: 3,
        fieldId: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.response.toString(),
        title: this.translateService.instant(
          'com.bmc.arsys.rx.client.approval.console.questions.grid.column.response.title'
        ),
        filterable: false,
        sortable: false
      },
      {
        index: 4,
        fieldId: RX_RECORD_DEFINITION.coreFieldIds.id.toString(),
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.id.label'),
        visible: false,
        filterable: false
      },
      {
        index: 5,
        fieldId: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.attachment.toString(),
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.attachment.label'),
        clickable: true,
        actions: [
          {
            name: (previousAction: any, selectedRow: IRowDataItem) => {
              this.rxRecordInstanceService.downloadAttachment(
                RX_APPROVAL_CONSOLE.requestDetailsGrid.questionDefinition,
                Number(RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.attachment),
                selectedRow[RX_RECORD_DEFINITION.coreFieldIds.id].split('|')[0],
                selectedRow[RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.attachment]
              );
            }
          }
        ]
      }
    ];
  }

  private getQuestionRecordDefinition(): IRecordDefinition {
    return {
      fieldDefinitions: [
        {
          id: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.to,
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character
        },
        {
          id: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.from,
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character
        },
        {
          id: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.question,
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character
        },
        {
          id: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.response,
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character
        },
        {
          id: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.attachment,
          resourceType: RX_RECORD_DEFINITION.resourceTypes.attachment
        },
        {
          id: RX_RECORD_DEFINITION.coreFieldIds.id,
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character
        }
      ]
    };
  }

  private showQuestionDetails(selectedRow: IRowDataItem) {
    this.adaptModalService
      .open({
        title: this.translateService.instant('com.bmc.arsys.rx.client.approval.console.question-response-dialog.title'),
        content: ReadOnlyFieldsModalComponent,
        size: 'sm',
        data: {
          fields: [
            {
              label: this.translateService.instant('com.bmc.arsys.rx.client.common.send-to.label'),
              value: selectedRow[RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.to]
            },

            {
              label: this.translateService.instant('com.bmc.arsys.rx.client.common.question.label'),
              value: selectedRow[RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.question]
            },
            {
              label: this.translateService.instant(
                'com.bmc.arsys.rx.client.approval.console.question-response-dialog.response-field.label'
              ),
              value:
                selectedRow[RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.response] ||
                this.translateService.instant(
                  'com.bmc.arsys.rx.client.approval.console.question-response-dialog.not-responded.message'
                )
            }
          ]
        }
      })
      .catch(noop);
  }
}
